import { Injectable } from '@angular/core';
import {Observable, of, throwError} from 'rxjs';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {catchError, map} from 'rxjs/operators';
import {User} from '../models/User';

@Injectable({
  providedIn: 'root'
})
export class ApiService {
  apiURL = 'http://213.35.139.201/api/v1/';

  httpOptions = {
    headers: new HttpHeaders({ 'Content-Type': 'application/json' })
  };
  constructor(private http: HttpClient) { }
  loginUserOnAPI(userLogin: any): Observable<User>{
    return this.http.post(
      this.apiURL + 'users/authenticate', userLogin, this.httpOptions
      ).pipe(
      map( (response) => {
        return response['data'];
      }),
      catchError( error => {
        return throwError(error);
      })
    );
  }
  registerUserOnAPI(user: any): Observable<User>{
    return this.http.post(
      this.apiURL + 'users/register', user, this.httpOptions
      ).pipe(
      map( (response) => {
        return response['data'];
      }),
      catchError( error => {
        return throwError(error);
      })
    );
  }

  loadAllUsersFromAPI(): Observable<User[]>{
    return this.http.get(
      this.apiURL + 'users/', this.httpOptions
      ).pipe(
      map( (response) => {
        return response['data'];
      }),
      catchError( error => {
        return throwError(error);
      })
    );
  }
}

