import { Component, OnInit } from '@angular/core';
import {ApiService} from '../api.service';
import {User} from '../../models/User';
import {Router} from '@angular/router';

@Component({
  selector: 'app-goodtimers',
  templateUrl: './goodtimers.component.html',
  styleUrls: ['./goodtimers.component.css']
})
export class GoodtimersComponent implements OnInit {
  goodTimers: User[];
  constructor(private apiService: ApiService, private router: Router) { }

  ngOnInit(): void {
    this.loadAllUsers();
  }

  loadAllUsers() {
    this.apiService.loadAllUsersFromAPI()
      .subscribe((response) => this.handleResponse(response));
  }

  handleResponse(response: User[]) {
    this.goodTimers = response;
  }

  showGoodTimerProfile(goodTimer: User) {
    console.table(goodTimer);
    this.router.navigate(['/profile', goodTimer] );
  }
}
